.alert-confirm-container * {
    box-sizing: border-box;
    margin: 0;
    padding: 0
}

.alert-confirm-container .alert-confirm-animation-in.alert-confirm-mask {
    animation: fadeIn 200ms forwards
}

.alert-confirm-container .alert-confirm-animation-in.react-alert-main {
    animation: zoomIn 200ms forwards
}

.alert-confirm-container .alert-confirm-animation-out.alert-confirm-mask {
    animation: fadeOut 200ms forwards
}

.alert-confirm-container .alert-confirm-animation-out.react-alert-main {
    animation: zoomOut 200ms forwards
}

.alert-confirm-mask {
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1000;
    background-color: rgba(0, 0, 0, .5);
    transition: background-color .2s;
    display: flex;
    align-items: center;
    justify-content: center
}

.react-alert-confirm {
    margin-top: -20vh;
    width: 410px;
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 0 3px 6px -4px rgba(0, 0, 0, .1215686275), 0 6px 16px rgba(0, 0, 0, .0784313725), 0 9px 28px 8px rgba(0, 0, 0, .0509803922);
    max-width: 90vw
}

.alert-confirm-body {
    padding: 28px 24px 22px;
    min-height: 70px
}

.alert-confirm-title {
    display: block;
    overflow: hidden;
    color: #555;
    font-size: 16px;
    font-weight: 500;
    line-height: 1.4;
    padding-bottom: 4px
}

.alert-confirm-desc {
    color: #777;
    font-size: 13px;
    padding-top: 4px
}

.alert-confirm-desc:first-child {
    padding-top: 0
}

.alert-confirm-footer {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: 0 24px 24px;
    font-size: 12px
}

.alert-confirm-footer>* {
    margin-left: 10px
}

.alert-confirm-button {
    border: 0;
    padding: 0 16px;
    line-height: 36px;
    cursor: pointer;
    color: #5c5c5c;
    border-radius: 4px;
    outline: none;
    font-size: 14px;
    background-color: #fff;
    transition: box-shadow .3s cubic-bezier(0.4, 0, 0.2, 1), color .3s;
    box-shadow: 0 -2px 5px 0 rgba(223, 200, 241, .3), 0 1px 0 0 rgba(223, 224, 241, .3), 0 1px 1px 0 rgba(162, 166, 191, .5), 0 2px 4px 0 rgba(228, 229, 242, .2)
}

.alert-confirm-button.default-button:hover,
.alert-confirm-button.default-button:focus {
    box-shadow: 0 2px 4px 0 rgba(162, 166, 191, .2), 0 2px 4px 0 rgba(162, 166, 191, .2), 0 4px 5px 0 rgba(162, 166, 191, .1);
    color: #148bed
}

.alert-confirm-button.primary-button {
    color: #fff;
    background-color: #148bed
}

.alert-confirm-button.primary-button:hover,
.alert-confirm-button.primary-button:focus {
    box-shadow: 0 3px 7px 0 rgba(139, 162, 236, .2), 0 2px 4px 0 rgba(86, 112, 216, .2), 0 4px 5px 0 rgba(71, 96, 196, .3)
}

.alert-confirm-button.danger-button {
    background-color: #e3524e;
    color: #fff
}

.alert-confirm-button.danger-button:hover,
.alert-confirm-button.danger-button:focus {
    box-shadow: 0 3px 7px 0 rgba(209, 129, 129, .2), 0 2px 4px 0 rgba(221, 130, 130, .2), 0 4px 5px 0 rgba(206, 85, 85, .3)
}

.alert-confirm-button:active {
    filter: brightness(96%)
}

@keyframes fadeIn {
    0% {
        opacity: 0
    }

    100% {
        opacity: 1
    }
}

@keyframes fadeOut {
    0% {
        opacity: 1
    }

    100% {
        opacity: 0
    }
}

@keyframes zoomIn {
    0% {
        transform: scale(0.6) translateY(-100px)
    }

    100% {
        transform: scale(1) translateY(0)
    }
}

@keyframes zoomOut {
    0% {
        transform: scale(1) translateY(0);
        opacity: 1
    }

    100% {
        transform: scale(0.6) translateY(-100px);
        opacity: 0
    }
}